import "materialize-css";
import "../index.css";
import { useState } from "react";
import { Pagination, PaginationButton } from "react-materialize";

export default function Paginator({ currentPage, setCurrentPage, cantRepos }) {

    const [input, setInput] = useState(1);

    const nextPage = () => {
        setInput(input + 1);
        setCurrentPage(currentPage + 1);
    }

    const previusPage = () => {
        setInput(input - 1);
        setCurrentPage(currentPage - 1);
    }

    return (
        <div class="conteiner center">
            <Pagination
                items={cantRepos}
                leftBtn={<PaginationButton onSelect={previusPage} ><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-arrow-left-square" viewBox="0 0 16 16">
                    <path fill-rule="evenodd" d="M15 2a1 1 0 0 0-1-1H2a1 1 0 0 0-1 1v12a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1V2zM0 2a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v12a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V2zm11.5 5.5a.5.5 0 0 1 0 1H5.707l2.147 2.146a.5.5 0 0 1-.708.708l-3-3a.5.5 0 0 1 0-.708l3-3a.5.5 0 1 1 .708.708L5.707 7.5H11.5z" />
                </svg></PaginationButton>}
                maxButtons={1}
                rightBtn={<PaginationButton onSelect={nextPage}><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-arrow-right-square" viewBox="0 0 16 16">
                <path fill-rule="evenodd" d="M15 2a1 1 0 0 0-1-1H2a1 1 0 0 0-1 1v12a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1V2zM0 2a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v12a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V2zm4.5 5.5a.5.5 0 0 0 0 1h5.793l-2.147 2.146a.5.5 0 0 0 .708.708l3-3a.5.5 0 0 0 0-.708l-3-3a.5.5 0 1 0-.708.708L10.293 7.5H4.5z"/>
              </svg></PaginationButton>}
            />
        </div>
    )
}